@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;1,300&display=swap');
@import url('./components/layout/GlobalStyle.css');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Titillium Web', sans-serif;

    scrollbar-width: none;
    /* For Firefox */
    -ms-overflow-style: none;
}

html,
body {
    background-color: var(--color-black2);
    width: 100%;
    height: 100%;
}

#root {
    width: 100%;
    height: 100%;
    background-image: url("img/back.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow-y: scroll;
}

*::-webkit-scrollbar {
    display: none;
}

h1 {
    font-size: 3em;
    margin: 0.5em;
    padding: 0.5em;
}

span {
    font-size: 1.5em;
    margin: .25em;
    padding: .25em;
}

@media only screen and (width <=1069px) {
    * {

        font-family: 'Bodoni Moda', serif;
        font-family: 'Roboto Mono', monospace;
    }

    #root {
        background-image: url("img/backSmall.jpg");
    }
}


@media only screen and (width <=750px) {
    h1 {
        font-size: 2em;
    }

    span {
        font-size: 1em;
    }
}